    display: flex;
    flex-wrap: wrap;
    p {
      padding-bottom: 25px;
      line-height: 40px;
      // text-indent: 25px;
      font-size: 22px;
      color: #333;
      strong {
        display: inline-block;
        padding: 0 6px;
      }
