  }
  .description {
    display: flex;
    flex-wrap: wrap;
    span {
      cursor: pointer;
      width: 100px;
      padding: 5px 10px;
      em {
        font-style: normal;
      }
      &:hover {
        color: #3bcaff;
      }
    }
  }
}
